.sidebar-responsive{
  width: 51px !important;
  
  
}

.sidebar-responsive ul li {
  position: relative !important;
  white-space: nowrap;
  cursor: pointer;
}

li svg{
  margin-right: 15px !important;
}

svg {
  cursor: pointer;
}

.lista{
  color: white;
  list-style: none;
}

.flex{
  display:flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.user-card{
  padding: 20px;
  color: white;
  border: 1px solid #ccc;
  margin: 1%;
  width: 30%;
  box-shadow: 0px 0px 10px #000;
}

.table-cont{
  width: 100%;
  max-width: 100%;
  font-size: 13px;
  word-wrap: break-word !important;
  word-break: break-all !important;
}

table{
  width: 100%;
}

table tbody td{
  color:#000;
  font-weight: 600;
  max-width: 30ch;
  vertical-align: middle;
  white-space: nowrap !important;
  overflow: hidden !important;
  text-overflow: ellipsis !important;
}

.wordBreak{
  word-wrap: break-word;
}

.action{
  font-size: 13px;
}

.edit{
  margin-right: 5px;
  color: white;
  background-color: rgb(34, 113, 204);
  padding: 5px;
  border-radius: 4px;
  display: flex;
  border: none;
}

.delete{
  margin-right: 5px;
  color: white;
  background-color: rgb(228, 25, 25);
  padding: 5px;
  border-radius: 4px;
  display: flex;
  border: none;
}

.detalhe{
  margin-right: 5px;
  color: #fff ;
  background-color: rgb(94, 94, 94);
  padding: 5px;
  border-radius: 4px;
  display: flex;
  border: none;
}

.flex-display{
  display: flex;
}

.btn{
    background-color: #398439;
    color: #fff;
    padding: 7px 10px;
    margin-bottom: 20px;
    text-decoration: none;
    transition: .5s;
    font-size: 13px;
    font-weight: 600;
    cursor: pointer;
    border: none;
    border-radius: 5px;
}

.btn:hover{
  background-color: #2d692d;
  font-weight: bold !important;
}


.form-pergunta{
  padding: 20px;
}

.titlePage{
  font-weight: bold;
  margin-right: 10px;
}

label {
  margin-bottom: .6em;
  font-weight: bold;
  font-size: 14px;
}

input, select {
  padding: .5em;
  border-radius: 0;
  border: 1px solid #ccc;
  font-size: 14px;
  font-weight: 600;
}

option{
  font-size: 14px;
  font-weight: 600;
}

input::placeholder{
  color: #7b7b7b;
}

.success {
  color: #fff;
  background-color: #00df30;
  font-weight: 600;
  transition: all 0.2s !important;
}

.error{
  color: #fff;
  background-color: #bd0010;
  font-weight: 600;
}

.section-index{
  background: rgb(255, 163, 42);
    width: 800px;
    height: 400px;
    display: flex;
    left: 50%;
    top: 50%;
    margin-left: -400px;
    margin-top: -200px;
    position: absolute;
    border-radius: 25px;
    flex-direction: column;
    justify-content: space-around;
    /* margin: auto; */
    text-align: center;
    transition: 5s ease-in-out;
}

.section-index-title h1{
  color: #fff;
  font-size: 72px;
}

.section-index-start button{
  padding: 15px;
  border-radius: 25px;
  width: 150px;
  background-color: #ffffff;
  border: none;
  transition: 0.2s;
  color: #fff;
}

.section-index-start button:hover{
  background-color: #976c34;
}

.section-index-start{
  display: flex;
  justify-content: space-evenly;
}

.start-section{
  padding: 10px 30px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.start-section h2{
  text-align: center;
  color: #fff;
}

.start-rules-section{
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  justify-content: space-between;
  width: 100%;
  padding: 15px;
  color: #fff;
  font-weight: bold;
}

.start-rules-section ol li{
  margin-top: 10px;
  color: #fff;
}

.start-button-section button, .score-button-section button{
  align-self: center;
  margin-top: 20px;
  font-size: 14px;
  color: #fff;
  background-color: #fdba31;
  border-radius: 15px;
  display: flex;
  padding: 7px;
  justify-content: center;
  align-items: center;
  border: none;
  cursor: pointer;
  width: 100px;
  text-align: center;
  font-weight: bold;
  transition: .2s;
}

.start-button-section button:hover, .score-button-section button:hover{
  background-color: #a77000;
}

.end-section{
  display: flex;
  flex-direction: column;
  align-content: center;
  padding: 10px;
  justify-content: space-evenly;
}

.score-section{
  display: flex;
  font-size: 24px;
  align-items: center;
  color: #fff;
}

.score-msg-section{
  display: flex;
  flex-direction: column;
  color: #fff;
}

.score-msg-section p{
  font-size: 18px;
  margin-top: 15px;
}

.score-msg-section h2, .score-msg-section p{
  text-align: center;
  color: #fff;
  font-weight: bold;
}

.score-button-section{
  display: flex;
  justify-content: center;
}

.question-section{
  width: 100%;
  position: relative;
  color: #fff;
  display: flex;
  flex-direction: column;
  padding: 5px;
}

.question-count{
  margin-bottom: 20px;
  color: #fff;

}

.question-count span{
  font-size: 28px;
  color: #fff;
  font-weight: bold;
}

.question-text{
  margin-bottom: 12px;
  color: #fff;
  font-weight: bold;
}

.timer-text{
  background: rgb(230,153,12);
  padding: 15px;
  margin-top: 20px;
  margin-right: 20px;
  border: 5px solid rgb(255,189,67);
  border-radius: 15px;
  text-align: center;
  color: #fff;

}

.answer-section{
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 5px;
}

.answer-section button{
  width: 100%;
  font-size: 14px;
  color: #fff;
  background-color: #fdba31;
  border-radius: 25px;
  display: flex;
  padding: 10px;
  justify-content: center;
  align-items: center;
  border: none;
  cursor: pointer;
  transition: 0.2s;
  font-weight: bold;
}

.correct{
  background-color: #2f922f;
}

.incorrect{
  background-color: #ff3333;
}

.answer-section button:hover{
  background-color: #a77000;
}

.answer-section button:focus{
  outline: none;
}

li::marker{
  font-weight: bold;
}

.question-assets{
  display: flex;
  width: 100%;
  justify-self: flex-end;
}

.question-assets-jump ul, .question-assets-discard ul{
  display: flex;
  list-style: none;
  justify-content: space-between;
}

.question-assets-jump ul li, .question-assets-discard ul li{
  margin: 5px;
}

.question-assets-jump svg path{
  color: #fff;
}

.question-assets button{
  background-color: #eee;
  border: none;
  padding: 5px 10px;
}

.quizGame{
  background-color: #a059f0;
  width: 600px;
  min-height: 250px;
  height: min-content;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 0px 0px 9px 0px rgba(0,0,0,0.5);
  display: flex;
  justify-content: space-evenly;
}

.dificultie-section h2{
  color: #fff;
}

.start-dificultie-section ul{
  list-style: none;
  padding: 0;
}

.start-dificultie-section ul li div button{
  width: 100%;
}
